// 
.logo-link {
    white-space: nowrap;
    text-indent: 101%;
    overflow: hidden;
}

// letter space
.letter-space-1 {
    letter-spacing: 0.04rem;
}


// background gradient
.bg-dark-gradient {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
}

// opacity
.opacity-70{
    opacity: 0.7;
}

// position
.left-22{
    left: 22px;
}
.left-44{
    left: 44px;
}

// z-index
.z-1{
    z-index: 1;
}